  table thead tr th,
  table tfoot tr th,
  table tfoot tr td,
  table tbody tr th,
  table tbody tr td,
  table tr td {
    display: table-cell;
    line-height: 1.125rem; }

/*Ensures a max width for the entire site and centres it in the event that
somebody loads the site on a wide monitor, which would stretch the images
beyond what is visually attractive*/
* {
  max-width: 1250px;
  margin-left: auto;
  margin-right: auto; }

/*sets the margin, font, background colour and width for the entire body*/
body {
  margin: 0;
  font-family: 'Oswald', sans-serif;
  background-color: white;
  max-width: 100vw; }

/*Sets the colour for the 'treeUK' logo in the header as well as setting
a padding of 0*/
.logo {
  color: #709255;
  background-color: #172815;
  padding: 0; }

/*Removes the bullet points from the treeList list*/
.treeList {
  list-style: none; }

/*Ensures the header stretches the width of the page, adds the background
colour and positions it on top of other elements*/
.header {
  background-color: #709255;
  width: 100%;
  overflow: hidden;
  z-index: 3;
  position: relative; }

/*Sets the style for the nav elements, removing the bullet points from
the list and reducing the margin and padding to 0*/
.header ul {
  margin: 0;
  padding: 0;
  list-style: none; }

/*Positions the nav elements next to each other inline, whilst adding
padding around each link, colouring them white, adding a background colour
and removing the text decoration*/
.header li a {
  display: block;
  padding: 20px 20px;
  color: white;
  background-color: #709255;
  text-decoration: none; }

/*on hover over the nav links in the header the background colour and
font colour will change to let the user know that the link is active*/
.header li a:hover,
.header .menu-btn:hover {
  background-color: white;
  color: #3E5622; }

/*Positions the logo in the header, sets the font size, padding and takes
away the default text-decoration*/
.header .logo {
  display: block;
  float: left;
  font-size: 2em;
  padding: 10px 20px;
  text-decoration: none; }

/*As this is a mobile-first responsive design, this section of css
refers to the drop down menu from the hamburger icon when the page
is open in a small window. It ensures that the menu drops down below
the header and then sets the transition setting for the drop down
action*/
.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height .2s ease-out;
  max-width: 100%; }

/*Positions the menu hamburger icon*/
.header .menu-icon {
  cursor: pointer;
  display: inline-block;
  float: right;
  padding: 28px 20px;
  position: relative;
  user-select: none; }

/*creates the hamburger icon and sets the transition settings for when
it is clicked*/
.header .menu-icon .navicon {
  background: #172815;
  display: block;
  height: 2px;
  position: relative;
  transition: background .2s ease-out;
  width: 18px; }

/*The following settings create the transition effect for when the hamburger
icon is clicked. The three lines will turn into a cross by rotating the top
and bottom lines and removing the middle one altogether. This indicates to
the user that the menu can be opened and closed by clicking the same icon.*/
.header .menu-icon .navicon:before,
.header .menu-icon .navicon:after {
  background: #172815;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all .2s ease-out;
  width: 100%; }

.header .menu-icon .navicon:before {
  top: 5px; }

.header .menu-icon .navicon:after {
  top: -5px; }

.header .menu-btn {
  display: none; }

.header .menu-btn:checked ~ .menu {
  max-height: 100%; }

.header .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent; }

.header .menu-btn:checked ~ .menu-icon .navicon:before {
  transform: rotate(-45deg);
  background: #3E5622; }

.header .menu-btn:checked ~ .menu-icon .navicon:after {
  transform: rotate(45deg);
  background: #3E5622; }

.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0; }

/*The banner image underneath the header. This ensures the image fits
inside the container perfectly without distorting. It sets the height
and ensures it stretches the width of the page*/
.banner {
  min-width: 100%;
  max-width: 100%;
  height: 300px;
  object-fit: cover; }

/*This ensures that the images that act as links on the home page and
tree plant project page stretch the width of the container that they are
in*/
.columnImages {
  min-width: 100%; }

/*removes the default underline on the link*/
.columnImages a {
  text-decoration: none; }

/*fits the image in the div container without distorting it*/
.imageCover {
  min-width: 100%;
  max-width: 100%;
  height: 200px;
  object-fit: cover; }

/*Ensures there's some padding between the element above and the
first image link*/
.imageCoverOne {
  padding-top: 20px; }

/*sets some text styling for the text under the link images. Centre
aligns it and creates white text to make it pop from the green
background*/
.centerText {
  text-align: center;
  padding-bottom: 2px;
  color: white;
  background-color: #709255;
  margin-top: 0; }

/*changes the background colour of the links on hover*/
.centerText:hover {
  background-color: #172815; }

/*styles sign in buttons throughout the site with a different
background colour to the standard imported by Zurb Foundation*/
.signIn {
  background-color: #709255; }

/*changes the background colour on hover to communicate to the user
that it is clickable*/
.signIn:hover {
  background-color: #172815; }

/*removes the underline from links and changes the colour*/
.link {
  text-decoration: none;
  color: #709255; }

/*edits the font colour on hover of the link*/
.link:hover {
  color: #172815; }

/*pads out the login form entry boxes to ensure a gap between*/
.loginForm {
  padding-left: 20px;
  padding-right: 20px; }

/*adds padding to the register button on login and register forms*/
.registerButton {
  padding-top: 5px;
  padding-bottom: 5px; }

/*caps the link images at 90% the width of the div container, which
gives it a nice white margin. The image is then centered in the div
and a transition added which comes into effect on hover*/
.columnImage {
  max-width: 90%;
  margin: 0 auto;
  transition: transform .5s; }

/*on hover the link images are scaled slightly bigger which creates
a pop out of screen*/
.columnImage:hover {
  transform: scale(1.05); }

/*The text on the banner at the top of the page is styled, setting
centre alignment, white text, a dark green background colour and padding*/
.homeText {
  text-align: center;
  color: white;
  background-color: #172815;
  padding: 10px; }

/*Changes the size of the banner image on all admin pages. The styling isn't
as necessary here as regular viewers will not be able to see so the overlarge
image banners become more cumbersome than necessary*/
.adminBanner {
  min-width: 100%;
  max-width: 100%;
  height: 100px;
  object-fit: cover; }

/*Sets the footer styling with padding, font size and colours set
as well as pushing text to the left and right of the footer*/
.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 10px;
  background-color: #172815;
  color: #95B46A; }

/*sets the size of the map container and sets a centre alignment*/
.mapContain {
  width: 100%;
  height: 500px;
  text-align: center; }

/*ensures the iframe element that the map is in spreads the entire width
and height of the container*/
iframe {
  height: 100%;
  width: 100%; }

/*removes the default styling of the admin nav, centre aligns it and adds
padding*/
.adminnav {
  list-style-type: none;
  text-align: center;
  padding: 5px; }

/*changes the colour of the admin links on hover*/
.adminnav a {
  color: #172815; }

.paddingText {
  padding-left: 20px;
  padding-right: 20px; }

/*sets the styling for the forms, overriding some settings from
the Zurb Foundation package. This styles the forms to match the
overall design of the site with colours, sizes and margins edited*/
.form-box {
  max-width: 500px;
  margin: auto;
  padding: 50px;
  background: #ffffff;
  border: 10px solid #172815;
  color: #172815;
  background-color: #95B46A;
  margin-top: 20px;
  margin-bottom: 20px; }

/*styles the input elements of the forms*/
input, textarea {
  width: 100%;
  background-color: #172815;
  color: #95B46A; }

/*changes the font style for paragraph and table elements to make
them more readable*/
p, td, th {
  font-family: sans-serif; }

/*changes the header to a more traditional header with links
across the page if the browser window is made bigger or if seen
on a desktop rather than mobile device*/
@media (min-width: 60em) {
  .header li {
    float: left; }
  .header li a {
    padding: 20px 30px; }
  .header {
    width: 100%; }
  .header .menu {
    clear: none;
    max-height: none; }
  .header .menu-icon {
    display: none; } }
